/* selecting each and every element on the page and applying reset to it */

*,
*::after,
*::before {
	margin: 0;
	padding: 0;
	//border box inherited from body
	box-sizing: inherit;
}

html {
	//1rem = 10px 10px/16px = 62.5%
	font-size: 62.5%;
	//will increase or reduce the font-size based on the screen size in the future
}

body {
	//easier to calculate props: total  width and height will include paddings
	box-sizing: border-box;
	font-family: "Gilroy", "Inter", sans-serif;
}

// Below is part of reset.css
// http://meyerweb.com/eric/tools/css/reset/
// v2.0 | 20110126
// License: none (public domain)

// HTML5 display-role reset for older browsers
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

body {
	line-height: 1;
}

ol,
ul {
	list-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: "";
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

a {
	text-decoration: none;
}
